/*========== Basic ==========*/
html {
  font-size: 16px;
  height: 100%; }

body {
  font-family: 'ProximaNova', sans-serif;
  color: #fff;
  font-size: 1rem;
  line-height: 1.33;
  min-width: 320px;
  pointer-events: none;
  transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in-out, background-color 0.5s ease-in-out;
  height: 100%;
  overflow: hidden; }
  body.loaded {
    opacity: 1 !important;
    visibility: visible !important;
    pointer-events: all !important;
    background-color: #0a0a0d; }
  body.disabled {
    pointer-events: none !important; }

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none; }
  *:before, *:after {
    box-sizing: border-box; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

button {
  display: block;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  appearance: none;
  padding: 0;
  border-radius: 0;
  color: inherit;
  cursor: pointer; }

/*========== Layout ==========*/
.wrapper {
  height: 100%;
  position: relative;
  overflow: hidden; }
  .wrapper__content {
    height: 100%;
    position: relative;
    z-index: 2;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch; }
  .wrapper__background {
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%; }
    .wrapper__background img {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .wrapper__background::before, .wrapper__background::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 2;
      width: 100%;
      left: 0; }
    .wrapper__background::before {
      top: 0;
      height: 300px;
      background: linear-gradient(0deg, rgba(10, 10, 13, 0) 0%, rgba(10, 10, 13, 0) 100%); }
    .wrapper__background::after {
      bottom: 0;
      height: 100px;
      background: linear-gradient(180deg, rgba(10, 10, 13, 0) 0%, rgba(10, 10, 13, 0.25) 100%); }

.bonus {
  font-size: 30px;
  font-weight: 800;
  line-height: 1.125;
  text-transform: uppercase;
  text-align: center;
  display: block;
  line-height: 1;
  text-shadow: 0px 13px 8px rgba(0, 0, 0, 0.3); }
  .bonus_style-1 {
    color: #fcc911;
    background: linear-gradient(to top, #fb9906 2%, #FCDC15 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .bonus_style-2 {
    color: #4df617;
    background: linear-gradient(to top, #63f80b 2%, #3ef51f 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .bonus_style-3 {
    color: #f9ba17;
    background: linear-gradient(to top, #fb9906 2%, #FCDC15 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .bonus_style-4 {
    color: #c505af;
    background: linear-gradient(to top, #e80478 2%, #aa06da 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.screen {
  margin: 0 auto;
  max-width: 580px;
  width: 100%;
  height: 100%;
  min-height: 550px;
  position: relative;
  z-index: 2;
  padding: 40px 20px 20px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  overflow: hidden; }
  .screen__top {
    position: relative;
    width: 100%;
    max-width: 360px;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out; }
  .screen__center {
    width: 100%;
    max-width: 280px;
    position: relative; }
  .screen__bottom {
    width: 100%;
    max-width: 360px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out; }
    .screen__bottom img {
      width: 40px;
      height: 40px;
      margin: -4px; }
    .screen__bottom span {
      font-size: 0.625rem;
      opacity: 0.35;
      margin: 0 -10px -10px 0; }
  .screen__title {
    font-size: 28px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 800;
    text-shadow: 0px 13px 8px rgba(0, 0, 0, 0.3);
    margin-bottom: 30px;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out; }
  .screen__text1 {
    font-size: 1.125rem;
    margin-bottom: 5px; }
  .screen__text2 {
    font-size: 0.75rem; }
  .screen__player {
    display: block;
    position: absolute;
    width: 225px;
    top: -35px;
    left: 170px; }
  .screen__signature {
    display: block;
    width: 80px;
    height: auto;
    margin: 10px 0 0 90px; }
  .screen__chests {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px -15px 0;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out; }
  .screen__bonuses {
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    backdrop-filter: blur(5px);
    overflow: auto;
    -webkit-overflow-scrolling: touch; }
  .screen__bonuses-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out; }
    .screen__bonuses-content .bonus_style-4 {
      font-size: 38px; }
  .screen__bonus {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    visibility: hidden; }
  .screen__chest {
    margin: 0 15px 15px 0;
    flex: 1 1 calc(33.33% - 20px);
    position: relative; }
    .screen__chest::before {
      content: '';
      display: block;
      position: absolute;
      height: calc(100% - 10px);
      width: calc(100% - 10px);
      top: 5px;
      left: 5px;
      box-shadow: 0px 0px 30px 0px rgba(255, 255, 255, 0.75);
      transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out; }
    .screen__chest img {
      display: block;
      width: 100%;
      height: auto;
      position: relative;
      z-index: 2;
      transition: transform 0.15s ease-in-out, opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
      will-change: transform; }
    .screen__chest[data-touch] img, .screen__chest[data-hover] img {
      transform: scale(1.05); }

.screen-final {
  display: none;
  pointer-events: all;
  min-height: 100%;
  padding: 40px 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .screen-final__title {
    border: 1px solid #fff;
    padding: 0 10px;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;
    margin-bottom: 20px; }
  .screen-final__bonus:not(:last-child) {
    margin-bottom: 10px; }
  .screen-final__bonuses {
    margin-bottom: 20px; }
  .screen-final__timer {
    margin-bottom: 25px; }
    .screen-final__timer-title {
      font-weight: 700;
      display: block;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 10px; }
    .screen-final__timer-element {
      display: flex;
      justify-content: center; }

.timer {
  font-weight: 700;
  display: flex;
  font-size: 1.75rem;
  line-height: 1; }
  .timer__item {
    width: 1.8em;
    text-align: center;
    position: relative; }
    .timer__item:not(:last-child) {
      margin-right: 10px; }
  .timer__item-dots {
    position: absolute;
    right: -5px;
    top: -0.1em;
    transform: translateX(50%); }

.chest-active .screen__chest:not(.active) img, .chest-active .screen__chest:not(.active)::before {
  opacity: 0;
  visibility: hidden; }

.chest-active .screen__title {
  opacity: 0;
  visibility: hidden; }

.last-step-start .screen__top, .last-step-start .screen__bottom, .last-step-start .screen__bonuses-content, .last-step-start .screen__chests, .last-step-start .screen__title {
  opacity: 0;
  visibility: hidden; }

.last-step-end .screen {
  min-height: 100%;
  overflow: initial; }

.last-step-end .screen-final {
  display: flex; }

.last-step-end .screen__bonuses {
  pointer-events: all; }

.last-step-end .screen__top, .last-step-end .screen__bottom, .last-step-end .screen__bonuses-content, .last-step-end .screen__chests, .last-step-end .screen__title {
  display: none; }

.button {
  display: inline-flex;
  line-height: 50px;
  border-radius: 8px;
  padding: 0 30px;
  text-transform: uppercase;
  font-size: 1.25rem;
  font-weight: 700;
  color: #000;
  background-color: #fff;
  border: 1px solid #126789;
  background: linear-gradient(180deg, #a3e9de 0%, #10748f 100%);
  transition: opacity 0.15s ease-in-out; }
  .button[data-touch], .button[data-hover] {
    opacity: 0.8; }

.hand-icon {
  position: absolute;
  top: 105px;
  left: 20px;
  z-index: 2;
  pointer-events: none;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
  transform-origin: top;
  animation: hand 1s linear infinite; }
  .hand-icon.hidden {
    opacity: 0;
    visibility: hidden; }
  .hand-icon img {
    display: block;
    width: 60px;
    height: 60px;
    object-fit: contain; }

@keyframes hand {
  0% {
    transform: scale(1); }
  70% {
    transform: scale(0.9); }
  100% {
    transform: scale(1); } }
